body{
    color:rgb(255, 251, 251);
}
.listItem{
    background-color:rgb(10, 146, 187);
    height:40px;
    margin:10px 40px;
    font-size:19px;  
    border-radius:5px;
}
.listItem h4{
    padding: 7px;
    position: relative;
}
#delete{
    position:absolute;
    right:10px;
    cursor: pointer;
}
.listItem:focus{
    outline:none;
}
.listItem h4 input{
    background-color:transparent;
    border:0px;
    font-size:19px;
    color:white;
}
.listItem h4 input:focus{
    outline: none;
}