body{
  background-color: black;

}
.task{
 min-height: 400px;
 margin:40px auto;
 width:400px;
border:solid #0aa7bb ;
border-width: 2px;
}
#task-form input{
  background-color: black;
  border:solid black 1px;
  border-bottom-color: #0aa7bb;
  height:30px;
  color:white;
  padding:3px;
  font-size: 18px;
  margin:30px;
}
#task-form input:focus,#task-form button:focus
{
  outline: none;
}
#task-form button{
  height:50px;
  width:50px;
  border-radius: 7px;
  border:none;
  background-color: #0aa7bb;
  color:white;
  font-size: 18px;
}